.App {
    text-align: center;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.each-album {
    display: flex;
    align-items: center;
    justify-content: center;
}

.each-img {
    margin-left: 10px;
    margin-right: 10px;
    width: 150px;
}

.overme {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.width-100-per {
    width: 100%;
}

.crousal {
    a {
        text-decoration: none;
        display: inline-block;
        padding: 8px 16px;
        margin-top: -80px;
    }

    a:hover {
        background-color: #ddd;
        color: black;
    }

    .previous {
        background-color: #f1f1f1;
        color: black;
    }

    .next {
        background-color: #a5a1a1;
        color: white;
    }

    .round {
        border-radius: 50%;
    }
}